<template>
  <div>
    <!-- 选项卡 -->
    <!-- <div>
      <v-tabs
        centered
        v-model="tab"
        height="40"
        background-color="transparent"
        active-class="rounded grey darken-3 text-center white--text"
        class="mb-4">
        <v-tab>配装</v-tab>
        <v-tab>详情</v-tab>
      </v-tabs>
    </div> -->

    <!-- <v-tabs-items v-model="tab"> -->
    <!-- 配装 -->
    <!-- <v-tab-item key="tab-1"> -->
    <div :class="isMobile ? '' : 'd-flex justify-center'">
      <div>
        <!-- 左侧 -->
        <div
          :style="{ position: isMobile ? '' : 'fixed' }"
          :class="isMobile ? 'd-flex justify-center' : ''">
          <div style="width: 252px; padding: 10px">
            <!-- 模拟器 -->
            <div
              class="rounded"
              :style="{
                position: 'relative',
                padding: '16px',
                background: `#f1f1f1`,
                backgroundPosition: '-3px 0',
              }">
              <div style="position: relative">
                <div
                  style="
                    position: relative;
                    display: flex;
                    justify-content: space-between;
                    width: 200px;
                    height: 108px;
                  ">
                  <!-- 防具 -->
                  <div
                    style="
                      display: flex;
                      justify-content: space-between;
                      align-content: space-between;
                      flex-wrap: wrap;
                      width: 108px;
                      height: 108px;
                      background-color: transparent;
                    ">
                    <div
                      v-for="(item, index) in partData.slice(0, 9)"
                      :key="index"
                      @click="filterPart(item)"
                      @contextmenu.prevent="take(item, index)"
                      :style="{ position: 'relative', cursor: item.Type === 8 ? '' : 'pointer' }">
                      <div style="width: 32px; height: 32px; overflow: hidden">
                        <tooltip :item="item" :roles="roles" :parts="parts" />
                      </div>
                    </div>
                  </div>

                  <!-- 首饰 -->
                  <div
                    style="
                      display: flex;
                      flex-wrap: wrap;
                      flex-direction: column;
                      justify-content: space-between;
                      align-content: space-between;
                      width: 70px;
                      height: 108px;
                      background-color: transparent;
                    ">
                    <div
                      style="position: relative; cursor: pointer"
                      v-for="(item, index) in partData.slice(9, 13)"
                      :key="index"
                      @click="selectPart = item.Type"
                      @contextmenu.prevent="take(item, index + 9)">
                      <div style="width: 32px; height: 32px; overflow: hidden">
                        <tooltip :item="item" :roles="roles" :parts="parts" />
                      </div>
                    </div>
                  </div>
                </div>

                <!-- 重置 -->
                <v-btn
                  depressed
                  color="grey lighten-1"
                  @click="reset"
                  height="24"
                  min-width="24"
                  style="position: absolute; padding: 0; bottom: 0; right: 0">
                  <v-icon dense color="white">mdi-refresh</v-icon>
                </v-btn>
              </div>

              <!-- 角色+等级 -->
              <div class="d-flex justify-space-between mt-2">
                <div class="d-flex">
                  <img
                    class="rounded mr-1"
                    width="17"
                    height="17"
                    :src="require(`@/assets/images/ui/${selectRole}.jpg`)"
                    alt="" />
                  <div
                    :style="{
                      width: '17px',
                      height: '17px',
                      backgroundImage: `url('/image/caste.png')`,
                      backgroundPosition: `-${castePosition.x}px -${castePosition.y}px`,
                      backgroundSize: '170px',
                    }" />
                </div>
              </div>
            </div>

            <!-- <v-btn depressed dark block class="flex-grow-1 my-2 grey darken-3" @click="save">
              保存
            </v-btn> -->

            <!-- 属性 -->
            <div class="pb-6 text-caption">
              <v-subheader>属性统计</v-subheader>

              <v-divider class="mx-3 mb-2"></v-divider>

              <div class="px-5">
                <div class="d-flex justify-space-between my-1">
                  <span>防御力</span>
                  <span>{{ calculate(Def) }}%</span>
                </div>

                <div class="d-flex justify-space-between my-1">
                  <span>体力</span>
                  <span>{{ calculate(HP) }}%</span>
                </div>

                <!-- <div class="d-flex justify-space-between my-1">
                  <span>耐力</span>
                  <span>{{ 8 }}%</span>
                </div> -->

                <!-- <div class="d-flex justify-space-between my-1">
                  <span>精神力</span>
                  <span>{{ 7 }}%</span>
                </div> -->

                <!-- <div class="d-flex justify-space-between my-1">
                  <span>爆头率增加</span>
                  <span>{{ 6 }}%</span>
                </div> -->

                <div class="d-flex justify-space-between my-1">
                  <span>伤害范围减少</span>
                  <span>{{ calculate(Range_DMG) }}%</span>
                </div>

                <!-- <div class="d-flex justify-space-between my-1">
                  <span>一般移动速度增加</span>
                  <span>{{ 4 }}%</span>
                </div> -->

                <div class="d-flex justify-space-between my-1">
                  <span>移动速度增加</span>
                  <span>{{ calculate(Run_up) }}%</span>
                </div>

                <!-- <div class="d-flex justify-space-between my-1">
                  <span>快速攀登</span>
                  <span>{{ 2 }}%</span>
                </div> -->

                <div class="d-flex justify-space-between my-1">
                  <span>子弹数增加（以后增加）</span>
                  <span>+{{ Bullet_ea_UP }}</span>
                </div>

                <div class="d-flex justify-space-between my-1">
                  <span>生命力</span>
                  <span>{{ calculatedValue }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- 右侧 -->
        <div
          :style="{ marginLeft: isMobile ? '0' : '260px' }"
          style="margin: 0 0 10px 0; padding: 10px">
          <!-- 筛选 -->
          <div class="d-flex">
            <!-- 选择角色 -->
            <div
              :class="isMobile ? 'flex-grow-1' : ''"
              class="d-flex justify-space-between mb-3 mr-6">
              <v-select
                v-model="selectRole"
                :items="roles"
                :menu-props="{ offsetY: true }"
                item-color="grey darken-4"
                dense
                solo
                flat
                hide-details="false"
                :style="{
                  width: isMobile ? '100% ' : '',
                  maxWidth: isMobile ? '' : '150px',
                }">
                <template v-slot:append>
                  <v-icon>mdi-menu-down</v-icon>
                </template>

                <template v-slot:selection="{ item }">
                  <div class="d-flex align-center">
                    <v-avatar color="primary" size="19" class="mr-2">
                      <img :src="require(`@/assets/images/ui/${item.value}.jpg`)" alt="" />
                    </v-avatar>

                    <div
                      style="
                        font-size: 0.9em;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                      ">
                      {{ item.text }}
                    </div>
                  </div>
                </template>

                <template v-slot:item="{ item }">
                  <!-- 角色头像 -->
                  <v-avatar color="primary" size="24" class="mr-2">
                    <img
                      v-for="num in 4"
                      :key="num"
                      v-if="item.value === num - 1"
                      :src="require(`@/assets/images/ui/${num - 1}.jpg`)"
                      alt="" />
                  </v-avatar>

                  <span style="font-size: 0.9em">{{ item.text }}</span>
                </template>
              </v-select>
            </div>

            <!-- 选择等级 -->
            <div
              :class="isMobile ? 'flex-grow-1' : ''"
              class="flex-grow-1 d-flex justify-space-between mb-3">
              <v-select
                v-model="selectCaste"
                :items="caste"
                :menu-props="{ offsetY: true }"
                item-color="grey darken-4"
                dense
                solo
                flat
                hide-details="false"
                :style="{
                  width: isMobile ? '100% ' : '',
                  maxWidth: isMobile ? '' : '150px',
                }">
                <template v-slot:append>
                  <v-icon>mdi-menu-down</v-icon>
                </template>

                <template v-slot:selection="{ item }">
                  <div class="d-flex align-center">
                    <div
                      class="mr-2"
                      :style="{
                        width: '17px',
                        height: '17px',
                        backgroundImage: `url('/image/caste.png')`,
                        backgroundPosition: `-${item.x}px -${item.y}px`,
                        backgroundSize: '170px',
                      }" />

                    <div
                      style="
                        font-size: 0.9em;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                      ">
                      {{ item.text }}
                    </div>
                  </div>
                </template>

                <template v-slot:item="{ item }">
                  <div
                    class="mr-2"
                    :style="{
                      width: '18px',
                      height: '18px',
                      backgroundImage: `url('/image/caste.png')`,
                      backgroundPosition: `-${item.x}px -${item.y}px`,
                      backgroundSize: '170px',
                    }" />

                  <span style="font-size: 0.9em">{{ item.text }}</span>
                </template>
              </v-select>
            </div>
          </div>

          <!-- 搜索 -->
          <div
            :style="
              isMobile && {
                position: 'sticky',
                top: '60px',
                zIndex: '1',
              }
            ">
            <v-text-field
              v-model="keyword"
              dense
              solo
              hide-details
              color="initial"
              placeholder="搜索.."
              prepend-inner-icon="mdi-magnify"
              clearable
              style="margin-bottom: 20px; font-size: 0.8em" />
          </div>

          <!-- 列表/图标视图 -->
          <div class="list" :style="{ width: isMobile ? '100%' : '410px' }">
            <!-- 部位 -->
            <div v-for="(item, index) in filterList" :key="index" style="padding: 0">
              <!-- 图标 -->
              <div style="cursor: pointer" @click="put(item)">
                <tooltip :item="item" :roles="roles" :parts="parts" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- </v-tab-item>

      <v-tab-item key="tab-2">
        <detail :part-data="partData"></detail>
      </v-tab-item>
    </v-tabs-items> -->

    <!-- 对话框 -->
    <!-- <Dialog ref="dialogRef" :part-data="partData"></Dialog> -->
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import Tooltip from './components/tooltip.vue';
import Dialog from './components/dialog.vue';
import Detail from './components/detail.vue';
import Big from 'big.js';
import BigNumber from 'bignumber.js';

export default {
  components: { Tooltip, Dialog, Detail },

  data() {
    return {
      partData: [
        { Type: 8, Icon: 99998 },
        { Type: 0, Icon: 99990 },
        { Type: 1, Icon: 99991 },
        { Type: 4, Icon: 99994 },
        { Type: 2, Icon: 99992 },
        { Type: 7, Icon: 99997 },
        { Type: 3, Icon: 99993 },
        { Type: 5, Icon: 99995 },
        { Type: 10, Icon: 99910 },
        { Type: 6, Icon: 99996 },
        { Type: 12, Icon: 99912 },
        { Type: 13, Icon: 99913 },
        { Type: 11, Icon: 99911 },
      ],

      roles: [
        {
          text: '艾利克斯',
          value: 0,
        },
        {
          text: '柯莉儿',
          value: 1,
        },
        {
          text: '布莱克',
          value: 2,
        },
        {
          text: '比比',
          value: 3,
        },
      ],

      caste: [
        {
          text: '元首',
          value: 320,
          x: 1,
          y: 360,
        },
        {
          text: '上将',
          value: 318,
          x: 1,
          y: 342,
        },
        {
          text: '中将',
          value: 316,
          x: 1,
          y: 324,
        },
        {
          text: '少将',
          value: 314,
          x: 1,
          y: 306,
        },
        {
          text: '准将',
          value: 312,
          x: 145,
          y: 288,
        },
        {
          text: '上校',
          value: 310,
          x: 145,
          y: 270,
        },
        {
          text: '中校',
          value: 308,
          x: 145,
          y: 252,
        },
        {
          text: '少校',
          value: 306,
          x: 145,
          y: 234,
        },
        {
          text: '上尉',
          value: 304,
          x: 127,
          y: 216,
        },
        {
          text: '中尉',
          value: 302,
          x: 127,
          y: 198,
        },
        {
          text: '少尉',
          value: 300,
          x: 127,
          y: 180,
        },
        {
          text: '准尉',
          value: 298,
          x: 127,
          y: 162,
        },
        {
          text: '士官长',
          value: 109,
          x: 109,
          y: 144,
        },
        {
          text: '上士',
          value: 294,
          x: 109,
          y: 126,
        },
        {
          text: '中士',
          value: 292,
          x: 109,
          y: 108,
        },
        {
          text: '下士',
          value: 290,
          x: 109,
          y: 91,
        },
        {
          text: '士兵长',
          value: 288,
          x: 91,
          y: 73,
        },
        {
          text: '上等兵',
          value: 286,
          x: 91,
          y: 55,
        },
        {
          text: '二等兵',
          value: 284,
          x: 91,
          y: 37,
        },
        {
          text: '一等兵',
          value: 282,
          x: 91,
          y: 19,
        },
      ],

      parts: [
        {
          text: '头盔',
          value: 0,
        },
        {
          text: '军用眼镜',
          value: 1,
        },
        {
          text: '战术手套',
          value: 4,
        },
        {
          text: '战斗服',
          value: 2,
        },
        {
          text: '背包',
          value: 7,
        },
        {
          text: '弹药补充袋',
          value: 3,
        },
        {
          text: '军靴',
          value: 5,
        },
        {
          text: '皮肤',
          value: 11,
        },
        {
          text: '称号',
          value: 10,
        },
        {
          text: '饰品',
          value: 6,
        },
        {
          text: '项链',
          value: 13,
        },
        {
          text: '戒指',
          value: 12,
        },
      ],

      keyword: '',

      sort: '默认',

      selectPart: 0,

      selectRole: 0,

      selectCaste: 312,

      dialogRef: null,

      tab: 'tab-2',
    };
  },

  mounted() {},

  computed: {
    ...mapState('armor', ['armorList']),

    isMobile() {
      return this.$vuetify.breakpoint.xs; // 判断是否为超小屏幕（< 600px）
    },

    filterList() {
      let arr;

      // 当前部位+角色
      arr = this.armorList.filter(item => {
        return (
          item.Type === this.selectPart &&
          (item.Chr_Type === this.selectRole || item.Chr_Type === 9)
        );
      });

      // 搜索
      if (this.keyword?.trim()) {
        arr = arr.filter(item => {
          return _.includes(item.Name.toString().toLowerCase(), this.keyword.toLowerCase());
        });
      }

      return arr;
    },

    // 防御力
    Def() {
      return this.partData.reduce((acc, cur) => new Big(acc).plus(+(cur.Def || 0)).toNumber(), 0);
    },

    // 体力
    HP() {
      return this.partData.reduce((acc, cur) => new Big(acc).plus(+(cur.HP || 0)).toNumber(), 0);
    },

    // 伤害范围减少
    Range_DMG() {
      return this.partData.reduce(
        (acc, cur) => new Big(acc).plus(+(cur.Range_DMG || 0)).toNumber(),
        0
      );
    },

    // 移动速度增加
    Run_up() {
      return this.partData.reduce(
        (acc, cur) => new Big(acc).plus(+(cur.Run_up || 0)).toNumber(),
        0
      );
    },

    // 子弹数增加
    Bullet_ea_UP() {
      return this.partData.reduce(
        (acc, cur) => new Big(acc).plus(+(cur.Bullet_ea_UP || 0)).toNumber(),
        0
      );
    },

    // 生命力
    Health() {
      return this.partData.reduce((acc, cur) => new Big(acc).plus(+(cur.HP || 0)).toNumber(), 0);
    },

    // 基础血量+生命力
    calculatedValue() {
      switch (this.selectRole) {
        case 0:
          return this.selectCaste + 20 + this.Health;
        case 1:
          return this.selectCaste + this.Health;
        case 2:
          return this.selectCaste + 40 + this.Health;
        case 3:
          return this.selectCaste + 10 + this.Health;
        default:
          return null;
      }
    },

    // 等级坐标
    castePosition() {
      return this.caste.find(item => item.value === this.selectCaste);
    },
  },

  methods: {
    // 穿戴
    put(item) {
      const index = this.partData.findIndex(p => p.Type === item.Type);
      this.$set(this.partData, index, item);
    },

    // 脱下
    take(item, index) {
      this.$set(this.partData, index, {
        Type: item.Type,
        Icon: +item.Type.toString().padStart(5, '9'),
      });
    },

    filterPart(item) {
      if (item.Type === 8) return;
      this.selectPart = item.Type;
    },

    // 重置
    reset() {
      this.partData = [
        { Type: 8, Icon: 99998 }, // 武器
        { Type: 0, Icon: 99990 }, // 头盔
        { Type: 1, Icon: 99991 }, // 军用眼镜
        { Type: 4, Icon: 99994 }, // 战术手套
        { Type: 2, Icon: 99992 }, // 战斗服
        { Type: 7, Icon: 99997 }, // 背包
        { Type: 3, Icon: 99993 }, // 弹药补充袋
        { Type: 5, Icon: 99995 }, // 军靴
        { Type: 10, Icon: 99910 }, // 称号
        { Type: 6, Icon: 99996 }, // 饰品
        { Type: 12, Icon: 99912 }, // 戒指
        { Type: 13, Icon: 99913 }, // 项链
        { Type: 11, Icon: 99911 }, // 皮肤
      ];
    },

    save() {
      this.$refs.dialogRef.openDialog();
    },

    calculate(n) {
      if (!n) {
        return 0;
      }

      return new BigNumber(n).multipliedBy(new BigNumber(0.7813)).toFixed(1);
    },
  },

  watch: {
    selectRole: {
      deep: true,
      // immediate: true,
      handler(value) {
        this.reset();
      },
    },
  },
};
</script>

<style scoped>
.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(32px, 1fr));
  gap: 10px;
}
</style>
